@import "./common"; //common.scss //컴파일 안한 채로 불러오기 //react에서는 ./ 라고 제대로 명시해주기


html{
    font-size: 16px;
}

*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;

    font-family: 'Yeongdeok Blueroad';
}

ul li{
    list-style: none;
}

img{
    vertical-align: middle;
}
//========== ========== ========== ========== ==========
header{
    // outline: 1px solid red;

    position: fixed; top: 0; left: 0; z-index: 10;
    width: 100%;

    @include flex();
    background-color: $mainColor1;
    height: 90px;
    padding-left: 22px; 
    padding-right: 30px; 

    >div.headerContents{
        // outline: 1px solid red;

        width: 100%;
        @include flex(space-between);
        >h1.logo{
            >a{
                @include font(1.5rem, 400, white){
                    text-decoration: none;
                    font-family: HSGyeoulNoonkott20;
                    white-space: nowrap;
                }
            }
        }
        >button.menuBtn{
            // outline: 1px solid red;
            border: none;
            background: none;

            width: 36px; height: 27px;
            background: url('../img/icon_menu.svg') center center / 100% 100% no-repeat scroll;
            cursor: pointer;
        }
    }//div.headerContents
}//header

footer{
    position: fixed; bottom: 0; left: 0; z-index: 10;

    @include flex(space-between);
    width: 100%; height: 100px;
    border-top: 1px solid $mainColor2;
    background: #FFF;
    box-shadow: 0px -4px 4px 0px rgba(0, 0, 0, 0.25);
    padding: 0 17px; 

    .whiteNoiseUi{
        display: none;
        position: absolute; top: 0; right: 0; z-index: 10; transform: translateY(-120%);

        &.active{
            display: inline-block;
        }
    }
    
    >a.search{
        flex-shrink: 0;
        display: inline-block;
        width: 50px; height: 50px;
        >img{
            width: 100%; height: 100%;

        }
    }//a.search

    >div.nickname{
        flex-shrink: 0;
        width: 296px; height: 60px;
        border: 1px solid $mainColor2;
        border-radius: 60px;
        @include flex();
        @include font(2rem, inherit, $grayColor1);
        white-space: nowrap;
    }

    >div.whiteNoise{
        flex-shrink: 0;
        position: relative; top: 0; left: 0; z-index: 0;
        width: 70px; height: 70px; border-radius: 50%;
        background-color: $grayColor2;
        overflow: hidden;
        cursor: pointer;
        >div.bg{
            position: absolute; top: 0; left: 0; z-index: 0;
            width: 100%; height: 100%;
            >img{
                position: absolute; top: 0; left: 0; z-index: 0;
                width: 100%; height: 100%;
                object-fit: cover;
            }

            &::after{
                position: absolute; top: 0; left: 0; z-index: 1;
                content: '';
                display: inline-block;
                width: 100%; height: 100%;
                background-color: rgba(0,0,0,0.1);
            }
        }

        >img{
            position: absolute; top: 50%; left: 50%; z-index: 1; transform: translate(-50%, -50%);
            width: 40%; opacity: 80%;
        }
    }
}//footer




div.verticalMenu{
    // display: none;
    position: fixed; top: 0; right: 0; z-index: 12;
    transform: translateX(100%);
    width: 50%; height: 100%; max-width: 450px;
    background: #FFF;
    
    transition: .2s;
    
    &.active{
        transform: translateX(0%);   
        box-shadow: -7px 0px 4px 0px rgba(0, 0, 0, 0.25);   
    }



    >button.menuXBtn{
        // outline: 1px solid red;
        position: relative; top:33px; left: 21px; z-index: 0;
        width: 26px; height: 26px;

        border: none;
        background-color: none;
        background: url('../img/icon_x.svg') center center / 100% 100% no-repeat scroll;
        cursor: pointer;

        transition: .2s;
    }

    div.content{
        // outline: 1px solid red;
        padding: 90px 50px 0;
        transition: .2s;

        >.nicknameWrapper{
            // outline: 1px solid red;
            height: 50px;
            @include flex(flex-start);

            span.nickname{
                height: 100%; width: 100%;
                margin-right: 10px;
                padding: 0 17px;
                @include flex(flex-start);
                
                border: 1px solid $mainColor2;
                border-radius: 30px;

                @include font(1.5rem, inherit, $grayColor2){
                    font-family: 'Inter';
                    white-space: nowrap;
                }
            }

            button.nicknameModify{
                flex-shrink: 0;
                width: 55px; height: 100%;
                border: none;
                border-radius: 20px;
                background-color: $mainColor2;
                @include font(1rem, 600, white){font-family: 'Inter'};
                cursor: pointer;
            }
        }//.nicknameWrapper

        >ul.soundBtnWrapper{
            margin: 38px 0 48px;
            @include flex(){
                flex-wrap: wrap;
                gap: 26px 25px
            };
            width: 100%;
            >li.soundBtn{
                flex-shrink: 0;
                // outline: 1px solid red;
                width: 70px; height: 70px;
                border-radius: 50%;
                overflow: hidden;
                cursor: pointer;

                &:nth-of-type(1) {background: url('../img/whiteNoise_rain.jpg') center center / cover no-repeat scroll;;}
                &:nth-of-type(2) {background: url('../img/whiteNoise_wave.jpg') center center / cover no-repeat scroll;;}
                &:nth-of-type(3) {background: url('../img/whiteNoise_seagull.jpg') center center / cover no-repeat scroll;;}
                &:nth-of-type(4) {background: url('../img/whiteNoise_firewood.jpg') center center / cover no-repeat scroll;;}
                &:nth-of-type(5) {background: url('../img/whiteNoise_smallRiver.jpg') center center / cover no-repeat scroll;;}
                &:nth-of-type(6) {background: url('../img/whiteNoise_bird.jpg') center center / cover no-repeat scroll;;}

                >div.soundMark{
                    width: 100%; height: 100%;
                    position: relative; top: 0; left: 0; z-index: 0;
                    display: none;

                    &::before{
                        position: absolute; top: 0; left: 0; z-index: 0;
                        
                        content: '';
                        display: inline-block;
                        width: 100%; height: 100%;
                        background-color: rgba(0,0,0, 0.1);
                    }
                    >img{
                        position: absolute; top: 50%; left: 50%; z-index: 1;
                        transform: translate(-50%, -50%);
                        
                    }
                }//div.soundMark

                &.active{
                    div.soundMark{
                        display: inline-block;
                    }
                }
            }//li.soundBtn
        }//ul.soundBtnWrapper

        >.MyBookListWrapper{
            padding: 30px 0 32px;
            border-top: 2px solid rgba(85, 122, 70, 0.20);
            border-bottom: 2px solid rgba(85, 122, 70, 0.20);

            >.moveToList{
                height: 50px;

                border: 1px solid $mainColor2;
                border-radius: 30px;
                background-color: $mainColor2;

                margin-bottom: 27px;
                padding: 0 17px;

                @include flex(flex-start);
                @include font(1.5rem, inherit, white){
                    font-family: 'Inter';
                    white-space: nowrap;
                    text-decoration: none;
                }
                cursor: pointer;
                transition: .2s;

                &::before{
                    content: '';
                    width: 30px; height: 27px; margin-right: 10px;
                    // outline: 1px solid red;

                    background: url('../img/icon_heart.svg') center center / 100% 100% no-repeat scroll;
                }

                &::after{
                    content: '>';
                    display: inline-block;
                    transform: translate(8px, -1px);

                    transition: .2s;

                }

                &:hover{
                    &::after{
                        transform: translate(16px, -1px);

                    }
                }
                
            }//.moveToList

            >ul.MyBookList{
                li{
                    @include font(1.125rem, inherit, $grayColor2);
                    @include text();
                    margin-bottom: 16px;

                    @include flex(flex-start, center);

                    &::before{
                        flex-shrink: 0;
                        content: '';
                        display: inline-block;
                        width: 4px; height: 4px; border-radius: 50%;
                        background-color: $grayColor2;
                        margin-right: 10px;
                    }
                }
            }//ul.MyBookList
        }//.MyBookListWrapper

    }
    
    >.loginWrapper{
        position: absolute; bottom:20px; left: 20px; z-index: 0;
        >.loginGoogle{
            border: 1px solid #424242; border-radius: 30px;
            width: 250px; height: 50px; 
            padding: 0 19px;
            margin-bottom: 16px;
            @include flex(flex-start);
            @include font(1.125rem, 400, #424242){
                font-family: 'inter';
            };
            cursor: pointer;
            
            >div.right{
                width: 65px; height: 100%;
                @include flex(space-between);
                >p{
                    width: 26px; height: 26px;
                    background: url('../img/icon_googleLogo.svg') center center / 100% 100% no-repeat scroll;
                }
                &::after{
                    content: '';
                    display: inline-block;
                    width: 1px; height: 60%;
                    background-color: #424242;
                    margin-right: 11px;
                }
            }
        }//.loginGoogle
        
        >.loginKakao{
            // border: 1px solid #3B1F06; 
            border-radius: 30px;
            width: 100%; max-width: 250px; height: 50px;
            padding: 0 19px;
            @include flex(flex-start);
            @include font(1.125rem, 400, #3B1F06){
                font-family: 'inter';
            };
            background-color: #f9e000;

            cursor: pointer;
            @include text();
            
            >div.left{
                width: 65px; height: 100%;
                @include flex(space-between);

                >p{
                    width: 34px; height: 31px;
                    background: url('../img/icon_kakaoLogo.svg') center center / 100% 100% no-repeat scroll;
                    
                }
                &::after{
                    content: '';
                    display: inline-block;
                    width: 1px; height: 60%;
                    background-color: #3B1F06;
                    margin-right: 11px;
                }
            }
        }//.loginKakao
    }//.loginWrapper
}//div.verticalMenu

main{
    padding-top: 90px; //header 생각해서 padding 이 만큼 넣음
    padding-bottom: 180px;



    //Home컴포넌트
    div.HomeCompo{
        >div.popularBookWrapper{
            // outline: 1px solid red;
            width: 100%; height: 56.25vw; max-height: 450px; min-height: 375px;

            >.popularBookSwiper{
                position: relative; top: 0; left: 0; z-index: 0;
                width: 100%; height: 100%;
                overflow: hidden;
                
                >.swiper-wrapper{
                    width: 100%; height: 100%;
                    @include flex(flex-start);

                    >.swiper-slide{
                        position: relative; top: 0; left: 0; z-index: 0;
                        width: 100%; height: 100%;
                        background-color: rgba(255, 255, 255, 0.5);
                        cursor: pointer;
                        flex-shrink: 0;

                        >.bg{
                            position: absolute; top: 0; left: 0; z-index: 0;
                            width: 100%; height: 100%;

                            >img{
                                position: absolute; top: 0; left: 0; z-index: 0;
                                width: 100%; height: 100%;
                                object-fit: cover;
                                opacity: 30%;
                            }
                            &::after{
                                position: absolute; top: 0; left: 0; z-index: 1;
                                content: '';
                                display: inline-block;
                                width: 100%; height: 100%;
                                background-color: black;
                                opacity: 10%;
                            }
                        }//.bg

                        >figure.content{
                            position: absolute; top: calc(50% - 28px); left: 50%; z-index: 1; 
                            padding: 0 12.5%;
                            // outline: 1px solid red;
                            transform: translate(-50%, -50%);
                            width: 100%; 

                            @include flex(space-between);
                            transition: .2s;

                            >p{
                                flex-shrink: 0;

                                width: 220px; height: 280px;
                                border-radius: 5px; overflow: hidden;
                                box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
                                >img{
                                    width: 100%; height: 100%;
                                    object-fit: cover;
                                    object-position: top center;

                                }
                            }//p

                            >.right{
                                @include flex(flex-start, flex-start){flex-direction: column;}
                                margin-left: 85px;

                                >figcaption{
                                    // outline: 1px solid red;
                                    @include text();
                                    @include font(1.75rem);
                                    opacity: 80%;
                                    margin-bottom: 10px;

                                }

                                >span{
                                    @include font(0.875rem);
                                    opacity: 80%;
                                    @include text();
                                    &:not(:last-child){
                                        margin-bottom: 4px;
                                    }
                                    >code{
                                        font-family: 'Yeongdeok Blueroad';
                                    }
                                } 

                                >button{
                                    padding: 8px 14px 8px 9px;
                                    margin-top: 22px;
                                    border-radius: 20px;
                                    border: 1px solid $mainColor2;
                                    background: #FFF;


                                    @include font(0.75rem, 700, $mainColor2){
                                        font-family: Inter;
                                        white-space: nowrap;
                                    };
                                    cursor: pointer;

                                    &::after{
                                        content: '>';
                                        display: inline-block;
                                        transform: translate(4px, -1px);

                                        transition: .2s;

                                    }

                                    &:hover{
                                        &::after{
                                            transform: translate(8px, -1px);
    
                                        }
                                    }
                                }//button
                            }
                        }//figure.content

                    }
                }//.swiper-wrapper

                >.swiper-pagination{
                    // outline: 1px solid red;
                    position: absolute; bottom: 18px; left: 50%; z-index: 1; 
                    transform: translateX(-50%);
                    @include flex();

                    >span.swiper-pagination-bullet{
                        cursor: pointer;

                        &:not(:last-child){
                            margin-right: 16px;
                        }
                        width: 10px; height: 10px;
                        border-radius: 50%;
                        background-color: white;
                        opacity: 60%;

                        &.swiper-pagination-bullet-active{
                            opacity: 100%;
                        }

                    }
                }//.swiper-pagination
            }//.popularBookSwiper
        }//div.popularBookWrapper

        >article h2{
            text-align: center;
            @include font(1.5rem, 400);
            margin-bottom: 32px;
            white-space: nowrap;
        }



    }//div.HomeCompo



    //Search컴포넌트
    div.SearchCompo{
        
        >div.searchWrapper{
            margin: 50px 12.5% 70px;
           
        }//div.searchWrapper

        >div.bookWrapper{
            // outline: 1px solid red;
            margin: 0 12.5%;
            @include flex(){flex-direction: column};
            
            >ul.bookItemWrapper{
                // outline: 1px solid red;
                width: 100%;
                margin-bottom: 60px;
                @include flex(flex-start, flex-start){
                    flex-wrap: wrap;
                    column-gap: 5%;
                    row-gap: 40px;
                };
                
                >li.bookItem{
                    // outline: 1px solid red;
                    width: 30%; 
                    >figure{
                        // outline: 1px solid red;
                        @include flex(flex-start){flex-direction: column;}
                        >i{
                            position: relative; top: 0; left: 0; z-index: 0;
                            display: inline-block;
                            border: 1px solid $mainColor1; border-radius: 10px;
                            width: 100%; height: 26vw;
                            margin-bottom: 8px;
                            overflow: hidden;
                            
                            >img{
                                position: absolute; top: 0; left: 0; z-index: 0;
                                width: 100%; height: 100%;
                                object-fit: cover; object-position: top center;
                            }
                            >div.heart{
                                position: absolute; bottom: 10px; right: 8px; z-index: 1;
                                // outline: 1px solid red;
                                width: 27%; height: 21%;
                                cursor: pointer;

                                background: url('../img/icon_heart_notActive.svg') center center / 100% 100% no-repeat scroll;
                                transition: .1s;
                                &.active{
                                    background: url('../img/icon_heart_active.svg') center center / cover no-repeat scroll;
                                }
                            }//div.heart
                            
                        }//p
                        
                        >figcaption{
                            margin-bottom: 5px;
                            @include font(0.875rem, 700, $grayColor1){
                                font-family: 'inter';
                                text-align: center;
                            }
                            @include text();
                        }//figcaption
                        
                        >span{
                            &:not(:last-child){
                                margin-bottom: 2px;
                            }
                            @include text();
                            text-align: center;
                            @include font(0.625rem, 400, $grayColor2){
                            }
                            >code{
                                font-family: 'Yeongdeok Blueroad';
                            }
                        }//span

                    }//figure

                    cursor: pointer;
                    transition: .2s;

                    &:hover{
                        transform: translateY(-10px);
                    }
                }//li.bookItem

                >li.noResult{
                    width: 100%; height: 100%;
                    >img{
                        width: 100%;
                        opacity: 20%;
                    }
                }
            }//ul.bookItemWrapper

            >button.seeMoreBtn{
                border: 1px solid $mainColor2; border-radius: 30px;
                background-color: white;
                padding: 10px 38px;
                cursor: pointer;

                @include font(1.5rem, inherit, $grayColor1){
                    white-space: nowrap;
                }
            }//button.seeMoreBtn
        }//div.bookWrapper


    }//div.SearchCompo


    //Detail컴포넌트
    div.DetailCompo{
        >figure.bookInfoWrapper{
            // outline: 1px solid red;
            margin: 100px 12.5% 50px;
            height: 47vw; max-height: 430px;
            
            @include flex(space-between, flex-start);

            >i{
                border: 1px solid $grayColor2;
                width: 48%; height: 100%;
                >img{
                    width: 100%; height: 100%;
                    object-fit: cover; object-position: top center;
                }
            }//i
            
            >div.right{
                position: relative; top: 0; left: 0; z-index: 0;
                // outline: 1px solid red;
                width: 52%; height: 100%;
                margin-left: 20px;

                @include flex(flex-start, flex-start){flex-direction: column};

                >figcaption{
                    margin: 24px 0 2px;
                    @include font(2rem, 700, $grayColor1){
                        // white-space: nowrap;
                    }
                    @include text();

                }
                >span{
                    margin-bottom: 15px;
                    @include font(1.2rem, 400, $grayColor2);
                    @include text();
                    opacity: 80%;
                }

                >ul{
                    >li{
                        font-family: 'inter';
                        &:not(:last-child){
                            margin-bottom: 6px;
                        }
    
                        @include text();
                        @include font(.8rem, 400, $grayColor2);
                        >code{
                            font-family: 'inter';
                        }
    
                    }
                }

                >div.priceWrapper{
                    margin-top: 25px;

                    @include flex(flex-start, flex-start){flex-direction: column};
                    >strong{
                        margin-bottom: 7px;
                        @include font(1.5rem, 700, $mainColor2){
                            font-family: 'inter';
                            white-space: nowrap;
                        }
    
                    }//strong
    
                    >a{
                        margin-top: 7px;

                        border: 1px solid $mainColor2; border-radius: 30px;
                        background-color: white;

                        padding: 10px 25px 10px 10px;

                        @include flex(flex-start);
                        @include font(0.75rem, 700, $mainColor2){
                            font-family: 'Inter';
                            white-space: nowrap;
                            text-decoration: none;
                        }
                        cursor: pointer;
                        transition: .2s;

                        &::after{
                            content: '>';
                            display: inline-block;
                            transform: translate(8px, -1px);
        
                            transition: .2s;
        
                        }
        
                        &:hover{
                            &::after{
                                transform: translate(16px, -1px);
        
                            }
                        }
                    }
                }//div.priceWrapper
    
                >div.AddedInfo{
                    position: absolute; bottom: 10px; right: 10px;
                    @include flex(flex-start, flex-start){flex-direction: column;}

                    >span{
                        @include font(0.625rem, inherit, $grayColor2){
                            font-family: 'inter';
                            white-space: nowrap;
                        }

                        &:not(:last-child){
                            margin-bottom: 2px;
                        }

                        >code{
                            @include font(0.625rem, inherit, $grayColor2){
                                font-family: 'inter';
                                white-space: nowrap;
                            }
    
                        }
                    }
                }//div.AddedInfo

            }//div.right


        }//figure.bookInfoWrapper

        >div.descriptionAccordion{
            // outline: 1px solid red;
            margin: 0px 12.5% 60px;
            >h3{
                border: 1px solid $mainColor1;
                width: 100%; height: 60px;
                text-align: center;
                @include flex(){flex-direction: column;}

                background: #FFF;
                box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);

                cursor: pointer;

                @include font(1.25rem, 700, $mainColor2){
                }

                &:after{
                    content: '';
                    display: inline-block;
                    width: 22px; height: 12px;
                    margin-top: 4px;
                    background: url('../img/icon_belowArrow.svg');

                    transition: .1s;
                }

            }

            >div.description{
                // display: none;
                border: 1px solid $mainColor1;
                padding: 0 45px;
                height: 0px;
                line-height: 130%;
                overflow: auto;

                background: #FFF;
                box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
                
                @include text();
                @include font(1.2rem, 400, #333);
                white-space: pre-wrap;

                transition: .2s;

            }
            
            &.active{
                >h3{
                    &:after{
                        transform: rotateX(-180deg);
                    }
                }

                >div.description{
                    height: auto; max-height: 600px;
                    padding: 20px 45px 40px;
                }
            }
        }//div.descriptionAccordion
        
        >article h2{
            text-align: center;
            @include font(1.5rem, 400);
            margin-bottom: 32px;
            white-space: nowrap;
        }


    }//div.DetailCompo

    //MyList컴포넌트
    div.MyListCompo{
        margin: 0 12.5% 50px;
        >h2{
            // outline: 1px solid red;
            height: 50px;

            padding-bottom: 12px; padding-right: 15px;
            margin: 60px 0 10px;
            border-bottom: 2px solid rgba(85, 122, 70, .2);

            @include flex(flex-start);
            @include font(2rem, inherit, $grayColor2){
                font-family: 'Inter';
                white-space: nowrap;
            }
            // width: 250px;
            

            &::before{
                content: '';
                width: 30px; height: 27px; margin-right: 10px;
                // outline: 1px solid red;

                background: url('../img/icon_heart.svg');
            }
        }//h2

        >ul{
            >li{
                position: relative; top: 0; left: 0; z-index: 0;
                padding: 40px 0;
                border-bottom: 2px solid rgba(85, 122, 70, .2);

                >button.xBtn{
                    position: absolute; top: 40px; right: 0;
                    width: 16px; height: 16px;
                    cursor: pointer;
                    border: none; 
                    background: url('../img/icon_x.svg') center center / 100% 100% no-repeat scroll;
                }

                
                >figure.bookInfoWrapper{
                    @include flex(flex-start, flex-start);
                    >i{
                        flex-shrink: 0;
                        display: inline-block;
                        width: 170px; height: 216px;
                        margin-right: 30px;

                        border: 1px solid $grayColor2; border-radius: 20px;
                        overflow: hidden;

                        >img{
                            width: 100%; height: 100%;
                            object-fit: cover;
                        }
                    }//i

                    >div.right{
                        @include flex(flex-start, flex-start){flex-direction: column};
                        >figcaption{
                            @include font(1.5rem, 600, $grayColor1){
                                font-family: 'inter'
                            };
                            margin-bottom: 10px;
                            padding-right: 30px;
                            @include text();
                            
                        }
                        >span{
                            &:not(:last-child){
                                margin-bottom: 4px;
                            }
                            @include font(0.875rem, 400, $grayColor2){
                                font-family: 'inter'
                            };
                            @include text();
                            >code{
                                font-family: 'inter'
                            }
                            
                        }

                        >a.moveToDetail{
                            margin-top: 22px;

                            border: 1px solid $mainColor2; border-radius: 30px;
                            background-color: white;
    
                            width: 110px;
                            padding: 8px 8px;
    
                            @include flex(flex-start);
                            @include font(0.75rem, 700, $mainColor2){
                                font-family: 'Inter';
                                white-space: nowrap;
                                text-decoration: none;
                            }
                            cursor: pointer;
                            transition: .2s;
    
                            &::after{
                                content: '>';
                                display: inline-block;
                                transform: translate(8px, -1px);
            
                                transition: .2s;
            
                            }
            
                            &:hover{
                                &::after{
                                    transform: translate(16px, -1px);
            
                                }
                            }
                        }//a.moveToDetail

                        //===========
                        
                    }//div.right
                }//figure.bookInfoWrapper

            }//li
        }//ul
        
    }//div.MyListCompo
    
}//main








div.searchWrapper{
    margin: 32px 12.5% 90px;
    height: 60px;
    fill: #FFF;
    border: 1px solid $mainColor2; border-radius: 30px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    
    
    >form.searchLeftContents{
        height: 100%;
        // outline: 1px solid red;
        padding-right: 20px;
        @include flex(space-between);
        width: auto;


        >.searchToggleWrapper{
            width: 120px; height: 100%;
            border: 1px solid $mainColor2; 
            border-left: none;
            border-radius: 30px;
            padding: 10px 13px;

            >.searchToggle{
                position: relative; top: 0; left: 0; z-index: 0;
                height: 100%;
                background-color: #CFCFCF;
                border-radius: 30px;
                padding: 0 4px;
                @include flex(flex-start);

                >input#tOrA{
                    display: none;
                }

                >label{
                    position: relative; top: 0; left: 0%; z-index: 0; 
                    width: 32px; height: 32px; border-radius: 50%;
                    display: inline-block;
                    background-color: $subColor1;
                    @include flex();
                    cursor: pointer;
                    transition: .2s;

                    &::after{
                        content: 'title';
                        @include font(0.75rem, inherit, white);

                    }
                }//label 

                >input#tOrA:checked + label{
                    left: 100%; transform: translateX(-100%);
                    background-color: $subColor2;

                    &::after{
                        content: 'all';
                        @include font(0.75rem, inherit, white);

                    }
                }//input#tOrA:checked + label

            }//.searchToggle
        }//.searchToggleWrapper


        >input{
            width: 100%; height: 100%;
            margin-left: 1px; margin-right: 1px;
            padding-left: 20px;
            padding-right: 20px;
            border: none;
            @include font(1.25rem, inherit,rgba(0, 0, 0, .5)){
                font-family: inter;
            };
            &::placeholder{
                opacity: 50%;
            }
            &:focus{
                font-family: inter;
                outline: none;
            }
        }


        >button.searchIcon{
            flex-shrink: 0;
            // outline: 1px solid red;
            width: 27px; height: 27px;
            border: none;
            background: url('../img/icon_search.svg') center center / 100% 100% no-repeat scroll;
            cursor: pointer;
        }
    }//div.searchRightContents
}//div.searchWrapper



article.popularBlogReviewWrapper{
    margin-bottom: 80px;
    >h2{
        
    }

    >.popularBlogReviewSwiper{
        width: 100%; height: 350px;
        background-color: #eee;
        padding: 32px 0;
        overflow: hidden;

        >.swiper-wrapper{
            height: 100%; width: 100%;
            // outline: 1px solid red;
            // @include flex(flex-start);
            
            >.swiper-slide{
                // outline: 1px solid red;
                flex-shrink: 0;
                position: relative; top: 0; left: 0; z-index: 0;
                width: 100%; height: 100%;
                border-radius: 20px;
                background-color: white;
                box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);

                overflow: hidden;
                
                >.content{
                    position: absolute; top: 0; left: 0; z-index: 0;
                    width: 100%; height: 100%;
                    @include flex(flex-start, center){flex-direction: column;}
                    padding: 52px;
                    cursor: pointer;

                    >strong{
                        @include font(1.25rem, inherit, $grayColor1);
                        @include text();
                        text-align: center;

                        display: -webkit-box;
                        -webkit-box-orient: vertical;
                        -webkit-line-clamp: 2;
                        overflow: hidden;
                    }
                    >b{
                        @include font(1rem, inherit, $grayColor2);
                        @include text();
                        text-align: center;
                        margin: 12px 0 30px
                        
                    }
                    >p{
                        @include font(0.75rem, inherit, $grayColor2);
                        @include text();
                        text-align: center;

                        display: -webkit-box;
                        -webkit-box-orient: vertical;
                        -webkit-line-clamp: 3;
                        overflow: hidden;

                    }
                }//.content
                >span.itmeNumber{
                    position: absolute; top: 0; left: 0; z-index: 1;
                    border-radius: 0 0 15px 0;
                    width: 40px; height: 40px;
                    background-color: $mainColor1;
                    @include flex();
                    @include font(1rem, bold, white){
                        font-family: 'inter';
                    };
                }

                transition: .2s;

                &:hover{
                    transform: translateY(-20px);
                }
            }//.swiper-slide
        }//.swiper-wrapper
        
    }//.popularBlogReviewSwiper
}//article.popularBlogReviewWrapper



